}

button, input, optgroup, select, textarea {
  font: inherit;
  color: inherit;
  border: 0;
  border-radius: 0;
  margin: 0;
  background-color: inherit;
}

input {
  background-color: #fff;
}

input {
  -webkit-writing-mode: horizontal-tb !important;
  text-rendering: auto;
  color: -internal-light-dark(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  appearance: auto;
  background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
  -webkit-rtl-ordering: logical;
  cursor: text;
  margin: 0em;
  font: 400 13.3333px Arial;
  padding: 1px 2px;
  border-width: 2px;
  border-style: inset;
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: initial;
}

input:-internal-autofill-selected {
  appearance: menulist-button;
  background-color: rgb(232, 240, 254) !important;
  background-image: none !important;
  color: -internal-light-dark(black, white) !important;
}

.side_account .input_pw {
  border-top: 0;
  border-radius: 0 0 2px 2px;
}

.side_account .input_id, .side_account .input_pw {
  width: 100%;
  height: 30px;
  padding: 5px 7px;
  border-radius: 2px 2px 0 0;
}

input[type="text"], input[type="password"], input[type="tel"], input[type="email"], input[type="url"], input[type="search"] {
  border-radius: 2px;
  border: 1px solid rgba(0,0,0,0.15);
  outline: none;
  -webkit-appearance: none;
  vertical-align: middle;
}

button {
  appearance: auto;
  -webkit-writing-mode: horizontal-tb !important;
  text-rendering: auto;
  color: -internal-light-dark(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: default;
  background-color: -internal-light-dark(rgb(239, 239, 239), rgb(59, 59, 59));
  box-sizing: border-box;
  margin: 0em;
  font: 400 13.3333px Arial;
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: initial;
}

.side_account .account_submit .button_submit {
  width: 60px;
